.b-main {
  color: $light;
  padding: 58px 0 175px;
  position: relative;

  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: rgba($dark, .5);
    max-width: 100%;

    video {
      width: 100%;
    }
  }

  &__container {
    max-width: 835px;
    margin-left: 355px;
    z-index: 1;
    position: relative;
    margin-top: 72px;
    @include media(xl) {
      max-width: 100%;
      padding: 0 10%;
      margin-left: 0;
    }
    @include media(md) {
      padding: 0 16px;
    }
    @include media(sm) {
      margin-top: 55px;
      padding-left: 37px;
      padding-right: 41px;
    }
    @include media(xs) {
      padding: 0;
    }
  }

  &__title {
    @include font(36, 300);
    color: $accent;
    word-spacing: .25px;
    @include media(sm) {
      @include font(31);
      letter-spacing: .15px;
    }
    @include media(xs) {
      max-width: 250px;
      @include mx--auto;
      //letter-spacing: .15px;
    }
  }

  &__info {
    margin-top: 30px;
    @include flex;
    @include media(sm) {
      margin-top: 41px;
    }

  }

  &__text {
    @include font(20, 300);
    @include mr(32);
    max-width: 350px;
    line-height: 1.6rem;
    min-height: 110px;

    span {
      margin: -5px;
      display: inline-block;
      @include font(30, 400);
      color: $accent;
      transform: translate(11px, 8px);
      letter-spacing: .17px;
    }

    @include media(sm) {
      margin-right: 0;
    }
    @include media(xs) {
      letter-spacing: -.2px;
      max-width: 251px;
      margin-left: auto;
      margin-right: auto !important;
    }

  }

  &__buttons {
    @include flex;
    margin-top: 76px;
    position: relative;
    margin-left: -4px;

    &::after {
      @include pseudo;
      @include align(absolute, true);
      @include size(9px);
      @extend %circle;
      left: 46.5%;
      background: $accent;
      @include media(xl) {
        left: 38%;
      }
      @include media(lg) {
        display: none;
      }
    }

    @include media(sm) {
      display: block;
      margin-top: 28px;
    }
  }

  &__button {
    border-color: $accent;
    width: 367px;
    margin-right: 55px;
    position: relative;

    span {
      @include font(18);
      letter-spacing: 1px;
      line-height: rem(18);
    }

    @include media(sm) {
      width: 300px;
      @include mx--auto;
      margin-bottom: 41px;
      span {
        @include font(15);
        letter-spacing: .7px;
      }
    }

  }

  &__ask {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    z-index: 6;
    width: 60px;
    background: $main;
    @include flex(center);
    @include trs;
    @include media(xl) {
      @include size(72px);
      border-radius: 50%;
      top: -13px;
      right: -14px;
    }

    &--active {
      width: 82.7%;
      .b-main__cross--desktop {
        display: block;
      }
      @include media(xl) {
        width: calc(100% + 14px);
        height: calc(100% + 13px);
        border-radius: 0;
        .b-main__icon svg {
          display: none;
        }
        .b-main__cross {
          display: block;
        }
      }
      @include media(md) {
        width: calc(100% + 16px);
        height: calc(100% + 17px);
      }
    }
    @include media(md) {
      position: fixed;
    }
  }

  &__icon {
    cursor: pointer;
    position: relative;
    height: 100%;
    width: 60px;
    @include flex(center, center);

    &:hover {
      svg path {
        fill: $accent;
      }
    }

    &::before {
      @include pseudo;
      @include size(46px, 46px);
      background: $main;
      @extend %circle;
      z-index: 1;
      left: -9px;
      top: 48%;
      transform: translateY(-60%);
      @include media(xl) {
        display: none;
      }
    }

    @include media(xl) {
      @include size(72px);
      position: absolute;
      right: 0;
      top: 0;
    }

    svg {
      transform: translateY(-60%);
      position: relative;
      z-index: 2;
      @include media(xl) {
        transform: none;
      }
    }
  }

  &__form {
    @include mx--auto;
    margin-left: 9%;
    @include media(md) {
      margin: 0;
      width: 100%;
      height: 100%;
      padding: 0 16px 41px;
      overflow-y: scroll;
    }
  }

  &__cross {
    @include size(24px);
    cursor: pointer;
    display: none;
    position: relative;

    span {
      width: 100%;
      height: 1px;
      background: rgba($light, .4);
      position: absolute;
      top: 50%;

      &:first-child {
        transform: rotate(45deg);
      }

      &:last-child {
        transform: rotate(-45deg);
      }
    }

    &--desktop {
      position: fixed;
      right: 57px;
      top: 49px;
      @include media(xl) {
        display: none !important;
      }
    }
  }

  .swiper-pagination {
    display: none;
  }

  &__slider {
    @include media(sm) {
      padding-bottom: 25px;
    }

  }

  @include media(md) {
    padding: 65px 0 120px;
  }
  @include media(sm) {
    padding: 38px 0 52px;
    .swiper-pagination {
      display: block;
      bottom: 0;

      &-bullet {
        @include trs;
        @include size(8px);
        background-color: $light;
        opacity: 0.4;

        &-active {
          opacity: 1;
        }
      }
    }
  }

}
