.b-why {
  padding: 112px 0 137px;
  background: url("../img/ad-bg.jpg") no-repeat center center/cover;
  color: $light;
  margin-bottom: 170px;
  &__container {
    max-width: 1145px;
    @include mx--auto;
    @include media(lg) {
      max-width: 100%;
      padding: 0 1%;
    }
    @include media(sm) {
      padding: 0 25px;
    }
  }
  &__title {
    text-align: center;
    @include font(36,300);
    letter-spacing: .18px;
  }
  &__items {
    margin-top: 50px;
    @include flex(center, space-between);
    @include media(sm) {
      margin-top: 34px;
      display: block;
    }
  }
  &__item {
    &:first-child {
      max-width: 300px;
      margin-left: 17px;

    }
    &:last-child {
      max-width: 325px;
      margin-top: 23px;
      margin-left: 17px;
      transform: translateX(22px);
    }
    @include media(sm) {
      margin-left: 0 !important;
      transform: none !important;
    }

  }
  &__text {
    @include font(20,300);
    margin-top: 20px;
    padding-left: 7px;
    letter-spacing: -.07px;
    line-height: 25px;
    @include media(lg) {
      @include font(16)
    }

  }
  &__timelines {
    margin-top: 24.5%;
    @include flex(false,space-between);
    position: relative;
    margin-left: 25px;
    @include media(md) {
      margin-top: 50px;
      display: block;
    }

  }
  &__timeline {
    position: relative;
    max-width: 193px;
    letter-spacing: .4px;
    padding-top: 5px;
    &::before, &::after {
      @include pseudo;
    }
    &::before {
      overflow: hidden;
      left: 0;
      top: -25px;
      width: 15px;
      height: 15px;
      background: transparent;
      @extend %circle;
      border: 3px solid $accent;
    }
    &::after {
      height: 1px;
      width: 113%;
      background: $accent;
      left: 15px;
      top: -17.5px;
      @include media(lg) {
        width: 105%;
      }
      @include media(ipad ) {
        width: 95%;
      }
      @include media(md) {
        display: none;
      }
    }
    &:last-child {
      &::after {
        display: none;
      }
    }
    &--left {
      &::after {
        transform: rotate(-38deg);
        top: -130%;
        left: -15%;
        width: 196%;
        @include media(lg) {
          transform: rotate(-40deg);
          top: -134%;
          width: 187%;
        }
        @include media(ipad ) {
          left: -18%;
          width: 184%;
          top: -132%;
          transform: rotate(-42deg);
        }
      }
    }
    &:nth-child(4){
      transform: translateX(3%);
      &::after {
        width: 116%;
        @include media(lg) {
          width: 105%;
        }
        @include media(ipad) {
          width: 98%;
        }
      }
    }
    &:nth-child(5) {
      transform: translateX(7%);
      @include media(lg) {
        transform: translateX(5%);
      }
    }
    &--top {
      transform: translate(43%,-378%);
      &::before {
        top: calc(100% + 17px);
        left: -5%;
        @include size(21px);
        border-width: 4px;
        @include media(md) {
          left: 0;
          top: -25px;
        }
      }
      &::after {
        top: 245%;
        left: -27%;
        width: 141%;
        transform: rotate(56deg);
        @include media(lg) {
          transform: rotate(59deg);
          top: 243%;
          width: 133%;
          left: -28%;
        }
        @include media(ipad ) {
          left: -33%;
          transform: rotate(63deg);
        }
      }
    }
    @include media(md) {
      transform: none;
      @include mb(50);
    }
  }
  &__description {
    @include font(14,300);
    line-height: 16px;
    letter-spacing: .2px;
  }
  @include media(lg) {
    padding: 100px 0;
    margin-bottom: 50px;
  }
  @include media(sm) {
    padding-top: 88px;
    text-align: left;
    padding-bottom: 180px;
  }
}
