.b-rate {
  padding: 150px 0 105px;
  background: url("../img/rate-bg.jpg") no-repeat center center/cover;
  color: $light;
  overflow: hidden;
  margin-top: 200px;
  @include media(lg) {
    margin-top: 100px;
  }
  &__container {
    @include media(md) {
      max-width: 100%;
      padding: 0 16px;
    }
  }
  &__title {
    @include font(38,300);
    text-align: center;
  }
  &__battery {
    margin-top: 52px;
    @include size(590px, 260px);
    @include mx--auto;
    margin-bottom: 73px;
    position: relative;
    img{
      @include media(sm) {
        display: none;
      }
      &.mob {
        display: none;
        @include media(sm) {
          @include mx--auto;
          display: block;
          max-width: none;
          max-height: none;
          object-fit: inherit;
        }
        @include media(xs) {
          max-width: 288px;
          height: 590px;
        }
      }
    }
    @include media(sm) {
      @include size(auto);
    }
  }
  &__subtext {
    @include align(absolute,center);
    @include font(100,300);
    line-height: 65px;
    text-align: center;
    span {
      @include font(30,300);
      display: block;
    }
  }
  &__views {
    top: 54%;
    color: $dark;
    left: 14%;
    letter-spacing: -7px;
    span {
      margin-left: 30px;
      letter-spacing: 0;
    }
    @include media(sm) {
      top: 65%;
      left: 23%;
    }
    @include media(xs) {
      left: 15%;
    }
  }
  &__price {
    right: 9%;
    top: 55%;
    @include media(sm) {
      top: 12%;
      transform: translateX(50%);
      right: 50%;
    }
  }
  &__text {
    max-width: 720px;
    text-align: center;
    @include mx--auto;
    @include font(15);
  }

  &__button {
    width: 340px;
    border-color: $accent;
    margin-top: 67px;
    @include mx--auto;
    span {
      color: $accent;
    }
    &:hover {
      background: $accent;
      span {
        color: $dark;
      }
    }
    @include media(xs) {
      width: 300px;
    }
  }
  &__info {
    max-width: 750px;
    margin-top: 118px;
    @include mx--auto;
    @include flex;
    @include media(md) {
      display: block;
      margin-top: 50px;
    }
  }
  &__description {
    @include font(38);
    line-height: 2.9rem;
    text-align: right;
    color: $main;
    letter-spacing: -1.5px;
    margin-right: 127px;
    position: relative;
    &::after {
      @include pseudo;
      background: #334358;
      top: -6px;
      right: -66px;
      width: 1px;
      height: 83%;
      @include media(md) {
        display: none;
      }
    }
    @include media(md) {
      text-align: center;
      @include mx--auto;
      br {
        display: none;
      }
    }
    @include media(sm) {
      margin-bottom: 44px;
    }
  }
  &__item {
    @include font(16);
    line-height: 42px;
    &--active {
      color: $accent;
      position: relative;
      &::before, &::after {
        @include pseudo;
        top: 50%;
        transform: translateY(-50%);
        @include media(md) {
          display: none;
        }
      }
      &::before {
        @include size(15px);
        @extend %circle;
        border: 3px solid $accent;
        left: -70px;
      }
      &::after {
        width: 1977%;
        height: 1px;
        background: $accent;
        left: -2000%;
      }
    }
  }
  @include media(md) {
    padding: 100px 0 80px;
  }
}

