.select2 {
  &-search {
    display: none;
  }
  &-container {
    width: 100% !important;
    &--default, .select2-selection--single {
      background: transparent;
      border-radius: 10px;
    }
  }
  &-selection {
    &__rendered {
      color: $light !important;
      @include font(13,400);
      line-height: 25px !important;
      text-align: left !important;
    }
    &--single {
      border: 2px solid rgba($light, .7) !important;
    }
    &__arrow b {
      width: 8px !important;
      height: 5px !important;
      background: url("../img/arrow.svg");
      border: none !important;
    }
  }
  &-container--default .select2-selection--single .select2-selection__rendered {
  }
  &-container--default .select2-selection--single .select2-selection__arrow {
    right: 16px;
  }
  &-results__option--highlighted[aria-selected] {
    background: $main !important;
  }
  .select2-container--default .select2-selection--single {
    @include media(md) {
      height: 30px;
    }
  }
}
