.b-form {
  display: none;
  &__content {
    @include flex;
    @include media(md){
      display: block;
      max-width: 100%;
    }
  }
  &__items {
    width: 450px;
    margin-right: 44px;
    @include media(md) {
      width: 320px;
      @include mx--auto;
    }
  }
  &__label {
    width: 100%;
    @include flex;
    height: 30px;
    p {
      &:first-child {
        width: 140px;
        margin-right: 30px;
        text-align: right;
        @include font(14);
        margin-top: 5px;
        @include media(md) {
          text-align: left;
          margin-top: 0;
          margin-bottom: 5px;
          width: 100%;
        }
      }
      &:last-child {
        align-self: center;
        margin-left: 13px;
        @include media(md) {
          display: inline-block;
        }
      }
      &.req {
        align-self: flex-start;
        margin-left: 8px;
        @include media(md) {
          transform: translate(-10px, -10px);
        }
      }
      &.left {
        text-align: left;
      }
      &.center {
        text-align: center;
        display: inline-block;
        margin: 0 5px;
      }
    }
    @include mb(15) ;
    @include media(md) {
      display: block;
      text-align: left;
      height: auto;
    }
  }
  &__title {
    @include font(30,300);
    margin-bottom: 56px;
    margin-left: 95px;
    text-align: center;
    margin-top: -15px;
    @include media(md) {
      margin-top: 47px;
      margin-bottom: 35px;
      margin-left: 0;
    }
  }
  &__input, &__textarea {
    padding-left: 14px;
    background: transparent;
    border: 2px solid rgba($light, .7);
    border-radius: 10px;
    @include font(13,400);
    color: $light;
  }
  &__input {
    width: 280px;
    height: 32px;
    &--small {
      width: 108px !important;
      margin-left: -5px;
    }
    @include media(md) {
      width: 287px;
      height: 30px;
    }
  }
  &__textarea {
    min-height: 85px;
    padding-top: 14px;
    margin-left: -5px;
    width: 270px;
    @include media(md) {
      width: 287px;
    }
  }

  &__mess {
    padding: 34px 44px 46px 46px;
    //border: 2px solid $light;
    background: url("../img/form-bg.png");
    @include size(393px, 365px);
    margin-top: -5px;
    & .b-form__label p:first-child{
      width: 80px;
      letter-spacing: .07px;
      @include font(14);
      margin-top: 0;
      @include media(md) {
        width: 77px;
        @include font(13);
      }
    }
    & .b-form__label p:last-child {
      margin-left: 0;
    }
    position: relative;
    &::before {
      @include pseudo;
      z-index: 5;
      background: $light;
    }
    &::before {
      bottom: 0;
    }
    .select2-container {
      //width: 82px !important;
      height: 30px;
    }
    @include media(md) {
      background: none;
      width: 320px;
      padding: 0;
      @include mx--auto;
      margin-top: 37px;
      text-align: center;
      .b-form__input {
        width: 100%;
        height: 30px;
      }
    }
  }
  &__subtitle {
    @include font(16, $semi);
    margin-bottom: 24px;
  }
  &__subtitle +.b-form__label {
    @include media(md) {
      display: flex;
    }
  }
  &__button {
    @include size(171px,56px);
    @include mx--auto;
    margin-top: 40px;
    border: 3px solid $accent;
    transform: translate(44px, 30px);
    p {
      @include font(18);
    }
    &:hover {
      background: $accent;
      p {
        color: $dark;
      }
    }
    @include media(md) {
      transform: none;
    }
  }
  &__error {
    display: none;
    text-align: center;
    margin-top: 40px;
  }

}
