.b-format {
  padding: 182px 0 163px;
  background: #f0f0f0;

  &__title {
    @include font(38, 300);
    text-align: center;
    margin-bottom: 105px;
    @include media(md) {
      margin-bottom: 30px;
    }
  }

  &__container {
    position: relative;
    height: 678px;
  }

  &__bg {
    width: 1257px;
    @include align(absolute);
    left: 50%;
    transform: translateX(-50.8%);
    bottom: -5.5%;
    max-width: 100%;
    @include media(lg) {
      left: 0;
      transform: translateX(-8%);
    }

    img {
      max-width: none;
    }

    @include media(md) {
      display: none;
    }

  }

  &__blocks {
    position: absolute;
    @include flex;
    bottom: 12%;
    left: 5.2%;
    @include media(lg) {
      left: 5.6%;
    }
  }

  &__block {
    opacity: 0;
    @include trs;

    &--active {
      opacity: 1;
    }

    &--2 {
      transform: translateX(-20%);
    }

    &--3 {
      transform: translate(-46%, -7%);
    }

    &--4 {
      transform: translate(-81%, 20%);
    }

    &--5 {
      transform: translate(-89.5%, 14%);
    }

    &--6 {
      transform: translate(-141%, 53%);
    }
  }

  &__items {
    @include flex(false, space-between);
    @include mx--auto;
    max-width: 1031px;
    height: 78%;
    @include media(md) {
      display: none;
    }
  }

  &__item {
    text-align: center;
    width: 160px;
    cursor: pointer;
    @include trs;
    position: relative;
    z-index: 2;

    &--active {
      margin-top: -40px;

      svg path {
        fill: $dark;
      }

      .b-format__text {
        &::before {
          border-color: $accent;
        }
      }

      &.b-format__item--1 {
        .b-format__text {
          &::after {
            height: 325%;
          }

          &::before {
            top: 428%;
          }
        }
      }

      &.b-format__item--2 {
        .b-format__text {
          &::after {
            height: 350%;
          }

          &::before {
            top: 455%;
          }
        }
      }

      &.b-format__item--3 {
        .b-format__text {
          &::after {
            height: 245%;
          }

          &::before {
            top: 355%;
          }
        }
      }

      &.b-format__item--4 {
        .b-format__text {
          &::after {
            height: 545%;
          }

          &::before {
            top: 657%;
          }
        }
      }

      &.b-format__item--5 {
        .b-format__text {
          &::after {
            height: 545%;
          }

          &::before {
            top: 650%;
          }
        }
      }

      &.b-format__item--6 {
        .b-format__text {
          &::after {
            height: 445%;
          }

          &::before {
            top: 547%;
          }
        }
      }
    }

    &--1 {
      transform: translateX(2.2%);

      .b-format__text {

        &::after {
          height: 280%;
        }

        &::before {
          top: 385%;
        }
      }

      @include media(md) {
        transform: translateX(11.5%);
        .b-format__text {

          &::after {
            height: 220%;
          }

          &::before {
            top: 325%;
          }
        }
      }
      @include media(xs) {
      transform: translateX(5%)
      }
    }

    &--2 {
      transform: translateX(6%);

      .b-format__icon {
        margin-left: 18px;
      }

      .b-format__text {
        margin-top: 25px;

        &::after {
          height: 305%;
        }

        &::before {
          top: 410%;
        }
      }

      @include media(md) {
        transform: translateX(-3%);
        .b-format__text {

          &::after {
            height: 240%;
          }

          &::before {
            top: 345%;
          }
        }
      }

    }

    &--3 {
      transform: translateX(9%);

      .b-format__icon {
        margin-left: 15px;
      }

      .b-format__text {
        margin-top: 25px;

        &::after {
          height: 190%;
          top: 110%;
        }

        &::before {
          top: 300%;
        }
      }

      @include media(md) {
        transform: translateX(8%);
        .b-format__text {

          &::after {
            height: 120%;
          }

          &::before {
            top: 230%;
          }
        }
      }
      @include media(xs) {
        transform: translateX(0%)
      }

    }

    &--4 {
      transform: translateX(15%);

      .b-format__icon {
        margin-left: 5px;
      }

      .b-format__text {
        margin-top: 23px;

        &::after {
          height: 475%;
          top: 110%;
        }

        &::before {
          top: 590%;
        }
      }

      @include media(md) {
        transform: translateX(-5%);
        .b-format__text {

          &::after {
            height: 390%;
          }

          &::before {
            top: 500%;
          }
        }
      }

    }

    &--5 {
      transform: translateX(19%);

      .b-format__text {
        &::after {
          height: 480%;
        }

        &::before {
          top: 586%;
        }
      }
      @include media(md) {
        transform: translateX(-9.3%);
        .b-format__text {

          &::after {
            height: 380%;
          }

          &::before {
            top: 485%;
          }
        }
      }
      @include media(xs) {
        transform: translateX(-2.3%)
      }

    }

    &--6 {
      transform: translateX(27%);

      .b-format__text {
        &::after {
          height: 390%;
        }

        &::before {
          top: 495%;
        }
      }

      @include media(md) {
        transform: translateX(-9.3%);
        .b-format__text {

          &::after {
            height: 320%;
          }

          &::before {
            top: 425%;
          }
        }
      }
      @include media(xs) {
        transform: translateX(-1.3%)
      }
    }

    @include media(md) {
      width: auto;
      margin-bottom: 70px;
      &:hover {
        cursor: pointer;
        .b-format__img {
          filter: none;
        }
      }
    }

  }

  &__text {
    margin-top: 30px;
    max-width: 136px;
    text-align: center;
    line-height: 16px;
    @include font(14);
    @include mx--auto;
    padding-bottom: 30px;
    position: relative;

    &::after, &::before {
      @include pseudo;
      @include align(absolute, false, true);
    }

    &::after {
      width: 1px;
      background: #d1d1d1;
      top: 105%;
    }

    &::before {
      @include trs;
      @include size(11px);
      @extend %circle;
      border: 3px solid $main;
      z-index: 3;
      overflow: hidden;
    }

  }

  &__slider {
    padding-top: 55px;
    display: none;
    @include media(md) {
      display: block;
    }
  }

  &__img {
    z-index: 0;
    @include trs;
    filter: grayscale(1);
    img {
      z-index: 0;
      width: 100%;
      object-fit: inherit;
    }

    &--1 {
      width: 112%;

      img {
        height: 220px;
      }
    }

    &--2 {
      width: 165%;
      transform: translate(-10%, 2%);

      img {
        height: 200px;
      }
    }

    &--3 {
      width: 115%;

      img {
        height: 220px;
      }
    }

    &--4 {
      transform: translate(-15%, 38%);
      width: 125%;

      img {
        height: 170px;
      }
    }

    &--5 {
      transform: translate(-15%, 38%);
      width: 125%;

      img {
        height: 170px;
      }
    }
    &--6 {
      transform: translate(-20%, 85%);
      width: 115%;

      img {
        height: 120px;
      }
    }
    @include media(xs) {
      width: 100%;
      &--5 {
        width: 108%;
        transform: translate(-3%, 38%);
      }
      &--6 {
        width: 115%;
      }
    }
  }

  &__slide {
    position: relative;
  }

  & .swiper-pagination {
    bottom: 0;
    top: 0;
    &-bullet {
      @include size(8px);
      background: $dark;
      opacity: .2;
      @include trs;
      &-active {
        background: $dark;
        opacity: 1;
      }
    }
  }

}
