.b-works {
  background: #f0f0f0;
  padding-bottom: 125px;
  @include media(xl) {
    padding-bottom: 10%;
  }

  &__flag {
    @include mx--auto;
    text-align: center;

    img {
      margin-top: -19px;
    }
  }

  &__title {
    margin-top: 38px;
    @include font(38, 300);
    text-align: center;
    color: $main;
  }

  &__items {
    margin-top: 55px;
    @include flex(false, space-between);
    max-width: 973px;
    @include mx--auto;
    @include media(sm) {
      max-width: none;
      display: block;
      margin-left: 0;
      margin-right: 0;
    }
  }

  &__item {
    max-width: 140px;
    @include media(sm) {
      @include flex(center);
      max-width: none;
      text-align: left;
      @include mb(13);
    }
  }

  &__icon {
    @include size(122px);
    @extend %circle;
    @include flex(center, center);
    background: $accent;
    position: relative;

    svg {
      max-width: 49px;
      max-height: 49px;
    }

    @include media(md) {
      @include size(100px);
      @include mx--auto;
      svg {
        @include size(40px);
      }
    }
    @include media(sm) {
      @include size(74px);
      margin-left: 0;
      margin-right: 24px;
    }
  }

  &__number {
    position: absolute;
    top: 0;
    right: 0;
    @include flex(center, center);
    @include size(38px);
    @extend %circle;
    background: $light;
    border: 2px solid $dark;

    span {
      @include font(20, $semi);
    }

    @include media(sm) {
      @include size(30px);
      top: -3px;
      right: -8px;
      span {
        @include font(15);
      }
    }
  }

  &__text {
    margin-top: 20px;
    @include font(14);
    line-height: 17px;
    text-align: center;
    @include media(sm) {
      max-width: 192px;
      text-align: left;
      margin-top: 0;
    }
  }

  &__subtitle {
    text-align: center;
    display: block;
    margin-top: 26px;
    @include font(14);
    opacity: .4;
    @include media(sm) {
      margin-top: 47px;
    }
  }

  &__button {
    @include size(640px, 112px);
    background: $main;
    border-color: $main;
    margin-top: 67px;
    @include mx--auto;

    span {
      @include font(29, 300);
      @include media(md) {
        @include font(25);
      }
    }

    &:hover {
      background: $accent;
      border-color: $accent;

      span {
        color: $dark;
      }
    }

    @include media(md) {
      @include size(500px, 100px);
    }
    @include media(sm) {
      @include size(302px, 92px);
      margin-top: 36px;
      span {
        @include font(23);
        letter-spacing: normal;
      }
    }
  }

  @include media(sm) {
    padding: 0 8px 105px;
  }
}
