.b-checkbox {
  display: flex;
  align-items: center;
  position: relative;
  margin-right: 22px;

  &:first-child {
  }

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    &:checked + .b-checkbox__text::after {
      opacity: 1;
    }
  }

  &__text {
    font-size: 12px;
    position: relative;
    cursor: pointer;
    padding-left: 40px;
    transition: all .3s;
    text-align: left;
    &::before {
      content: '';
      @include size(27px);
      border: 2px solid $light;
      position: absolute;
      border-radius: 10px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &::after {
      content: '';
      position: absolute;
      @include size(15px);
      background: $light;
      border-radius: 5px;
      border-color: transparent;
      left: 6px;
      top: 50%;
      transform: translateY(-50%);
      opacity: 0;
      transition: .3s;
    }

  }

}
