.b-button {
  @include flex(center, center);
  @include trs;
  box-shadow: none;
  border-radius: 50px;
  cursor: pointer;
  text-align: center;
  border: 3px solid;
  height: 64px;
  &__content {
   @include font(18);
    letter-spacing: 1.09px;
    color: $light;
  }
  &--main {
    &:hover {
      background: $accent;
      span {
        color: $dark;
      }
    }
  }
  &--md {
    width: 230px;
    border-color: $light;
  }
  &--hide {
    opacity: .3;
    @extend .b-button--md;
    &:hover {
      border-color: $main;
      opacity: 1;
    }
    @include media(sm) {
      @include mx--auto;
    }
  }
}
