.b-select {
  width: 192px;
  @include flex(center, space-between);

  &__item {
    width: 100%;

    &--small {
      width: 82px;

      & + .select2-container {
        width: 82px !important;
        @include media(md) {
          width: 133px !important;
        }
      }
      @include media(md) {
        width: 133px;
      }
    }

  }
  @include media(md) {
    width: 320px;
  }
  @include media(xs) {
    width: 288px;
  }
}
