.b-header {
  &__container {
    position: relative;
    z-index: 1;
    max-width: 1381px;
    //margin-right: 132px;
    //margin-left: 87px;
    @include mx--auto;
    @include flex(false, space-between);
    color: $light;
    @include media(xl) {
      max-width: 100%;
      padding: 0 2%;
    }
    @include media(md) {
      padding: 0 16px;
      margin-right: 0;
      margin-left: 0;
    }
    @include media(sm) {
      display: block;
      text-align: center;
    }
  }

  &__contacts {
    @include flex;
    flex-direction: column;
    @include media(sm) {
      margin-top: 18px;
    }
  }
  &__phone {
    @include font(22, 300);
    letter-spacing: .11px;
    line-height: 1rem;

  }
  &__write {
    @include font(12,false);
    letter-spacing: .06px;
    display: block;
    text-align: right;
    text-decoration: underline !important;
    &:hover {
      color: $accent;
    }
    @include media(sm) {
      text-align: center;
      margin-top: 6px;
    }
  }
}
