.b-popup {
  display: none;
  &--active {
    display: block;
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 6;
    background: rgba($light,.7);
    cursor: pointer;
  }
  &__header {
    @include flex(center, space-between);
  }
  &__close {
    @include size(33px);
    position: relative;
    cursor: pointer;
    span {
      position: absolute;
      width: 100%;
      height: 1px;
      background: #c0c0c0;
      bottom: 50%;
      left: 0;
      @include trs;
      &:first-child {
        transform: rotate(45deg);
      }
      &:last-child {
        transform: rotate(-45deg);
      }
    }
    &:hover {
      span {
        background: #787878;
      }
    }
  }
  &__content {
    @include align(fixed, true, true);
    @include size(455px, 478px);
    background-color: #f0f0f0;
    box-shadow: 0 0 46px rgba($dark, 0.75);
    padding: 40px 37px 72px 60px;
    border-radius: 30px;
    z-index: 7;
    @include media(sm) {
      max-width: 98%;
      @include mx--auto;
      padding: 20px;
    }
  }
  &__title {
    @include font(30,300);
  }
  &__form {
    margin-top: 40px;
  }
  &__field {
    @include flex(false, space-between);
    @include mb(15);
    &:last-child {
      margin-bottom: 28px;
    }
    @include media(xs) {
      flex-wrap: wrap;
    }
  }
  &__thanks {
    display: none;
    margin-top: 10px;
    &--active {
      display: block;
    }
  }
  &__subtitle {
    @include font(13, $semi);
  }
  &__img {
    margin-top: 78px;
    text-align: center;
  }
  &__text {
    margin-right: 16px;
    &:first-child {
      @include font(13, $semi);
      width: 50px;
      margin-top: 8px;
      @include media(xs) {
        display: block;
        margin-bottom: 5px;
      }
    }
    &:last-child {
      @include font(14);
      opacity: .4;
      margin-left: 2px;
    }
  }
  &__input, &__textarea {
    width: 278px;
    border: 1px solid rgba($dark, .2);
    border-radius: 10px;
    box-shadow: none;
    padding-left: 12px;
    @include media(xs) {
      width: 90%;
    }
  }
  &__input {
    height: 30px;
    background: $light;
  }
  &__textarea {
    min-height: 125px;
    padding-top: 12px;

  }
  &__button {
    @include size(171px, 56px);
    background: $main;
    border-color: $main;
    margin-left: 20%;
    &:hover {
      background: $accent;
      border-color: $accent;
      span {
        color: $dark;
      }
    }
  }
  &__error {
    display: none;
    margin-top: 28px;
  }
}
