.b-speed {
  background: $lightGrey;
  padding: 135px 0 108px;
  text-align: center;
  @include media(xl) {
    padding: 100px 0 80px;
  }
  @include media(md) {
    padding: 70px 0 40px;
  }

  &__title {
    @include font(38, 300);
    @include media(sm) {
      @include font(31);
    }
    @include media(xs) {
      letter-spacing: -1px;
      line-height: 34px;
    }
  }

  &__info {
    line-height: 66px;
    margin-top: 30px;
    @include mx--auto;
    @include flex(center, center);
    @include media(sm) {
      margin-top: 40px;
      display: block;
    }
  }

  &__items {
    @include flex(center, space-between);
    margin-top: 55px;
    margin-bottom: 50px;
    max-width: 705px;
    @include mx--auto;
    @include media(sm) {
      display: block;
      max-width: none;
    }
  }

  &__item {
    @include flex(center);
    padding-left: 40px;

    &:nth-child(2) {
      .b-speed__name {
        margin-left: 22px;
      }
    }

    @include media(sm) {
      padding-left: 0;
      @include mb(23);
    }
  }

  &__icon {
    svg {
      max-width: none;
      max-height: none;
    }
  }

  &__name {
    margin-left: 27px;
    text-align: left;
    display: inline-block;
    @include font(21);
    letter-spacing: -.42px;
    @include media(sm) {
      @include font(19);
    }
  }

  &__text {
    color: $main;
    @include flex(center);

    & .small {
      @include font(30, 300);
      display: inline-block;
      text-align: left;
      max-width: 178px;
      line-height: .8;

      &:last-child {
        margin-right: 0;
        transform: translateX(25%);
        @include media(md) {
          transform: translateX(10%);
        }
        @include media(sm) {
          min-width: 100%;
          transform: none;
          margin-top: 35px;
          display: inline-block;
        }
      }

      @include media(md) {
        @include font(25);
      }
      @include media(sm) {
        @include font(30);
        @include mr(22);
      }

    }

    & .big {
      transform: translateX(12%);
      @include font(90, 300);
      margin-right: 30px;
      letter-spacing: -3.15px;

      &--margin {
        margin-right: 0;
        letter-spacing: -5.6px;
        transform: translateX(4%);
      }

      @include media(md) {
        @include font(60);
        margin-right: 15px;
      }
      @include media(sm) {
        margin-right: 0;
        transform: none;
      }
    }

    &:first-child {
      margin-left: -35px;
      @include media(sm) {
        margin-left: 0;
      }
    }

    &:last-child {
      transform: translateX(7%);
    }

    @include media(sm) {
      transform: none !important;
      justify-content: flex-start;
      flex-wrap: wrap;
      @include mb(30);
    }
  }

  &__small {
    @include font(13);
    @include mx--auto;
    margin-top: 35px;
    max-width: 830px;
    letter-spacing: .07px;

  }

  &__img {
    transform: translateX(20px);

  }

  &__button {
    @include mx--auto;
    margin-top: 45px;
    border-color: $main;
    @extend .b-button--md;

    span {
      color: $main;
      letter-spacing: 1.09px;
    }

    &:hover {
      background: $main;

      span {
        color: $light;
      }
    }
  }

  @include media(sm) {
    padding: 52px 33px 60px;
  }

  @include media(xs) {
    padding: 52px 16px 60px;

  }

}

.panorama {
  @include media(xs) {
    @include trs;
    height: 400px;
    overflow-x: scroll;
    overflow-y: hidden;
    img {
      height: 100%;
      max-width: none;
      max-height: none;
      width: auto;
      object-fit: inherit;
    }
  }

}
