.b-footer {
  background: #f0f0f0;
  height: 437px;
  @include flex(flex-end);
  padding-bottom: 53px;
  position: relative;
  &__container {
    width: 100%;
    @include flex(center, space-between);
    padding-top: 32px;
    max-width: 1377px;
    border-top: 1px solid #a8a8a8;
    @include mx--auto;
    position: relative;
    z-index: 1;
    @include media(xl) {
      max-width: 100%;
      padding: 2% 2% 0;
    }
    @include media(sm) {
      max-width: 100%;
      flex-direction: column;
      padding-top: 42px;
      margin-top: 100px;
    }
  }
  &__img {
    @include align(absolute,false,true);
    filter: grayscale(1);
    width: 100%;
    text-align: center;
    @include media(sm) {
      @include size(325px, 437px);
      top: 0;
      left: -22%;
      img {
        object-fit: inherit;
        max-width: none;
        max-height: none;
      }
    }
    @include media(xs) {
      left: -50%;
    }
  }
  &__logo {
    @include flex(center);
  }
  &__copy {
    display: flex;
    align-items: center;
    margin-top: -15px;
    @include font(20);
    small {
      @include font(13)
    }
    @include media(sm) {
      margin-top: 0;
      margin-left: 11px;
    }
  }
  &__button {
    margin-left: 50px;
    @include size(278px, 53px);
    border-color: rgba($dark, .4);
    span {
      opacity: .8;
      color: $dark;
      @include font(13, $semi);
    }
    &:hover {

      border-color: $main;
      span {
        color: $main;
        opacity: 1;
      }
    }
    @include mx--auto;
    @include media(sm) {
      order: 1;
      margin-top: 32px;
    }
  }
  &__contacts {
    align-self: flex-start;
    @include media(sm) {
      @include mx--auto;
      margin-top: 26px;
    }
  }
  &__phone {
    @include font(22)
  }
  &__write {
    text-decoration: underline !important;
    @include font(12);
    display: block;
    text-align: right;
    @include trs;
    &:hover {
      color: $main;
    }
    @include media(sm) {
      text-align: center;
    }
  }
  @include media(sm) {
    height: 675px;
  }
}
