.b-up {
  @include size(226px);
  background: $accent;
  padding: 50px 48px 20px;
  @extend %circle;
  position: fixed;
  bottom: 50px;
  text-align: center;
  right: -100%;
  z-index: 5;
  @include trs(all, 1s);
  &--active {
    right: 90px;

  }
  &__title {
    @include font(26,300);
    color: #300088;
    line-height: 26px;
  }
  &__text {
    margin: 10px 0 20px;
    @include font(18);
    line-height: 22px;
  }
  &::before {
    @include pseudo;
    @include size(246px);
    @extend %circle;
    border: 2px solid $light;
    opacity: .3;
    top: -10px;
    left: -10px;
  }
  &:hover {
    &::before {
      animation: hoverAnimation 1.3s ease infinite;
    }
  }
  @include media(sm) {
    position: relative;
    right: 0;
    left: 50%;
    transform: translateX(-50%);
    margin-top: -30%;
  }
}
@keyframes hoverAnimation {
  0% {
    @include size(230px);
    top: -2px;
    left: -2px;
  }
  25% {
    @include size(234px);
    top: -4px;
    left: -4px;
  }
  50% {
    @include size(240px);
    top: -7px;
    left: -7px;
  }
  100% {
    @include size(246px);
    top: -10px;
    left: -10px;
  }
}
